  width: max-content;
  z-index: 100;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background-color: #fff;
}

.square {
  background: #191919;
  color: inherit;
  border: 1px solid;
  float: left;
  /* font-size: 15px; */
  /* font-weight: bold; */
  /* line-height: 24px; */
