  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.root {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
}

.dot {
  height: 14px;
  width: 14px;
  border-radius: 50%;
  margin: 5px;
  display: inline-block;
}
/* robot in css to replace dot */
.robot {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.robot-head {
  background-color: #333;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.robot-eyes {
  display: flex;
  justify-content: space-between;
  width: 60%;
}

.robot-eye {
  background-color: #fff;
  width: 20px;
  height: 20px;
  border-radius: 50%;
}

.robot-antenna {
  background-color: #ff0000;
  width: 10px;
  height: 30px;
  position: absolute;
  top: -20px;
  border-radius: 5px;
}

.robot-body {
  background-color: #666;
  width: 120px;
  height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.robot-arm {
  background-color: #666;
  width: 20px;
  height: 60px;
  position: absolute;
  top: 20px;
}

.robot-arm:first-child {
  left: -30px;
}

.robot-arm:last-child {
  right: -30px;
}

.robot-leg {
  background-color: #666;
  width: 20px;
  height: 60px;
  margin-top: 10px;
}


.dot-2 {
  height: 14px;
  width: 14px;
  border-radius: 50%;
  margin: 5px;
  display: inline-block;
}

.buttons {
  cursor: pointer;
  z-index: 101;
  font-size: 20px;
}

.row:after {
  clear: both;
  content: "";
  display: table;
}

@-webkit-keyframes particle_ {
  from {
    transform: rotateX(90deg) rotateY(0deg);
  }

  to {
    transform: rotateX(90deg) rotateY(-360deg);
  }
}

@keyframes particle_ {
  from {
    transform: rotateX(90deg) rotateY(0deg);
  }

  to {
    transform: rotateX(90deg) rotateY(-360deg);
  }
}

