@-webkit-keyframes movementLeft {
  0% {
    left: -70px;
    top: 35px;
  }

  50% {
    left: 50px;
    top: -25px;
  }

  100% {
    left: -70px;
    top: 35px;
  }
}

@keyframes movementLeft {
  0% {
    left: -70px;
    top: 35px;
  }

  50% {
    left: 50px;
    top: -25px;
  }

  100% {
    left: -70px;
    top: 35px;
  }
}

@-webkit-keyframes movementTop {
  0% {
    top: -70px;
  }

  50% {
    top: 50px;
  }

  100% {
    top: -70px;
  }
}

