.loading {
  position: relative;
  width: 7px;
  height: 7px;
  border-radius: 5px;
  background-color: rgb(205, 0, 197);
  color: rgb(205, 0, 197);
  animation: dotFlashing 1s infinite linear alternate;
  animation-delay: .5s;
}

.loading::before,
.loading::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 0;
}

.loading::before {
  left: -15px;
  width: 7px;
  height: 7px;
  border-radius: 5px;
  background-color: rgb(205, 0, 197);
  color: rgb(205, 0, 197);
  animation: dotFlashing 1s infinite alternate;
  animation-delay: 0s;
}

.loading::after {
  left: 15px;
  width: 7px;
  height: 7px;
  border-radius: 5px;
  background-color: rgb(205, 0, 197);
  color: rgb(205, 0, 197);
  animation: dotFlashing 1s infinite alternate;
  animation-delay: 1s;
}


@keyframes dotFlashing {
  0% {
    background-color: rgb(205, 0, 197);
  }

  50%,
  100% {
    background-color: rgb(249, 212, 255);
  }
}

