.butt {
  position: relative;
  background-color: #191919;
  border-radius: 4em;
  color: white;
  user-select: none;
  text-align: center;
  text-decoration: none;
  margin-top: 5px;
  cursor: pointer;
  transition-duration: 0.4s;
  -webkit-transition-duration: 0.4s;
}

.butt:hover {
  transition-duration: 0.1s;
  background-color: #3A3A3A;
}

.butt:after {
  content: "";
  display: block;
  position: absolute;
  border-radius: 4em;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all 0.5s;
  box-shadow: 0 0 0px 3px #3A3A3A;
}

.butt:active:after {
  box-shadow: 0 0 0 0 #3A3A3A;
  position: absolute;
  border-radius: 4em;
  left: 0;
  top: 0;
  opacity: 1;
  transition: 0s;
}

.butt:active {
  top: 1px;
}

